:root {
  --font-primary: "Spartan", sans-serif;
}

:root {
  --color-primary: hsl(300, 43%, 22%);
  --color-bg-rating: hsl(300, 24%, 96%);
  --color-verified: hsl(333, 80%, 67%);
  --color-featured: hsl(303, 10%, 53%);
  --color-text-card: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 5rem 0;
  font-family: var(--font-primary);
  font-size: 0.9375rem;
  background: url(../images/bg-pattern-top-mobile.svg), url(../images/bg-pattern-bottom-mobile.svg);
  background-repeat: no-repeat;
  background-position: top left, bottom right;
}
@media (min-width: 48em) {
  body {
    background: url(../images/bg-pattern-top-desktop.svg), url(../images/bg-pattern-bottom-desktop.svg);
    background-repeat: no-repeat;
    background-position: top left, bottom right;
  }
}
@media (min-width: 64em) {
  body {
    padding: 2rem;
  }
}

main {
  width: 90vw;
  max-width: 43.75rem;
  display: grid;
  grid-template-columns: 100%;
}
@media (min-width: 64em) {
  main {
    grid-template-columns: repeat(2, 1fr);
    max-width: 68.75rem;
  }
}

@media (min-width: 64em) {
  .slogan {
    max-width: 28.125rem;
  }
}
.slogan__title {
  color: var(--color-primary);
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 48em) {
  .slogan__title {
    font-size: 3rem;
  }
}
@media (min-width: 64em) {
  .slogan__title {
    text-align: initial;
    margin-bottom: 1rem;
  }
}
.slogan__description {
  color: var(--color-featured);
  text-align: center;
  line-height: 1.5;
  font-weight: 500;
  letter-spacing: -0.01875rem;
}
@media (min-width: 64em) {
  .slogan__description {
    text-align: initial;
  }
}

.rating {
  display: flex;
  flex-direction: column;
  justify-content: initial;
  gap: 1rem;
  padding: 3rem 0;
}
@media (min-width: 64em) {
  .rating {
    padding: 0;
  }
}
.rating__component {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.25rem 2rem;
  background-color: var(--color-bg-rating);
  border-radius: 0.5rem;
}
@media (min-width: 48em) {
  .rating__component {
    display: flex;
    flex-direction: row;
    justify-content: initial;
    width: 75%;
  }
}
@media (min-width: 48em) {
  .rating__component:nth-child(2) {
    transform: translateX(3rem);
  }
}
@media (min-width: 48em) {
  .rating__component:nth-child(3) {
    transform: translateX(5.75rem);
  }
}
.rating__images {
  display: flex;
  flex-direction: row;
  justify-content: initial;
  gap: 0.3125rem;
  margin-bottom: 1rem;
}
@media (min-width: 48em) {
  .rating__images {
    margin-bottom: 0;
    margin-right: 2rem;
  }
}
.rating__stars {
  background: url(../images/icon-star.svg) no-repeat;
  height: 0.9375rem;
  width: 0.9375rem;
}
.rating__description {
  color: var(--color-primary);
  text-align: center;
}
@media (min-width: 64em) {
  .rating__description {
    font-size: 0.75rem;
  }
}

.review {
  display: flex;
  flex-direction: column;
  justify-content: initial;
  gap: 1.25rem;
}
@media (min-width: 64em) {
  .review {
    display: flex;
    flex-direction: row;
    justify-content: initial;
    grid-column: 1/3;
    margin-top: 4rem;
  }
}
.review__card {
  background-color: var(--color-primary);
  padding: 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 64em) {
  .review__card {
    width: 33%;
  }
}
@media (min-width: 64em) {
  .review__card:nth-child(2) {
    transform: translateY(1.5rem);
  }
}
@media (min-width: 64em) {
  .review__card:nth-child(3) {
    transform: translateY(3rem);
  }
}
.review__container {
  display: flex;
  flex-direction: row;
  justify-content: initial;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}
.review__photograph {
  border-radius: 50%;
  width: 2.5rem;
}
.review__name {
  color: var(--color-text-card);
  line-height: 1.5;
  font-size: 1rem;
  font-weight: 500;
}
.review__verified {
  color: var(--color-verified);
}
.review__description {
  color: var(--color-text-card);
  font-size: 0.8125rem;
  line-height: 1.7;
}